.main-nav {
  position: fixed;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  top: 0;
  left: -100%;
  width: 50%;
  height: 100vh;
  background-color: rgba(0,0,0, .9);
  transition: 0.5s all;
  z-index: 2;

  &__list {
    list-style: none;
    margin: 0;
    padding: 0;
  }

  &__item {
    padding-right: 0;
    margin: 20px 0;
  }

  &__link {
    position: relative;
    font-size: 16px;
    color: $main-color;

    &:hover {
      color: white;
      border-bottom: 2px solid #d55448;
    }
  }

  &__active {
    left: 0;
  }
}

@media (min-width: $tablet-width) {
  .main-nav {
    display: none;
  }
}
